

body{
  font-family: 'Noto Sans TC', sans-serif;
  line-height: 1.5;
}

.container{
  // box-sizing: border-box;
  margin: 0 auto;
  // font-family: 'Noto Sans TC', sans-serif;
}

img{
  max-width: 100%;
  height: auto;
}  
a{
  text-decoration: none;
  display: block;
  &:hover{
    opacity: 0.5;
    transition: 0.3s ease all;
  }
}


.main{
  .container{
    // width: 60%;
    width: 80%;
    @include iPhone6(){
      width: 90%;
    }

    h3.tit{
      font-size: $font-xl;
      color: $accent;
      text-align: center;
      font-weight: bolder;
      margin-bottom: 40px;
      @include iPad(){
        font-size:32px;
      }
      @include iPhone6(){
        font-size:$font-ml;
      }
    }
  }
}

